.contacts {
    &__wrap {
        background-color: #0d1e2d;
        @include clr; }
    .container {
        margin: 0 auto;
        padding: 30px 0;
        @include clr;
        @include r(620) {
            margin: 0 10px; } }
    &__touchus {
        width: 35.586206896551724137931034482759%;
        float: left;
        @include clr;
        @include r(620) {
            width: 100%; } }
    &__local {
        width: 43.862068965517241379310344827586%;
        float: right;
        @include clr;
        @include r(620) {
            width: 100%; } } }

.touchus__title {
    font-size: 12.18px;
    font-family: "Martel";
    color: #ffffff;
    line-height: 1.286;
    text-align: left; }
.touchus__form {
    display: block;
    width: 100%;
    line-height: 0.979;
    @include clr;
    input, textarea {
        width: 100%;
        height: 30px;
        @include r(620) {
            margin: 10px auto; } }
    textarea {
        height: 90px;
        margin-top: 20px; }
    input[type="submit"] {
        font-size: 16px;
        font-family: "Martel";
        background-color: #ffa326;
        color: #ffffff;
        font-weight: bold;
        line-height: 0.979;
        text-align: center; } }


.local {
    &__address, &__call, &__phone {
        font-size: 18.52px;
        font-family: "Martel";
        color: #ffffff;
        line-height: 0.979;
        text-align: center;
        margin: 15px 0; }
    &__call {
        padding: 15px 0;
        float: left; }
    &__phone {
        display: block;
        float: left;
        font-size: 46.83px;
        color: #ffa326;
        font-weight: bold;
        text-decoration: none;
        @include r(620) {
            font-size: 42px; } }
    @include r(620) {
        float: left; } }

#map {
    max-width: 100%;
    float: left;
    img {
        width: 100%; } }

form, input {
    margin: 0;
    padding: 0; }
